.player {
  position: absolute;
  width: 35px;
  top: 15px;
  left: 15px;
  cursor: pointer;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.modal-content {
  position: relative;
  width: 40%;
  min-height: 200px;
  padding: 25px;
  background-color: rgba(27, 11, 36, 0.59);
  color: white;
}

.close-container {
  position: absolute;
  top: -50px;
  right: -40px;
  width: 50px;
  height: 50px;
}

.close {
  font-size: 45px;
  font-weight: 600;
  display: inline-block;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  cursor: pointer;
}

.wrapper {
}

.logo-container {
  background: #eebb5c;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
}

ul {
  padding-left: 0;
  list-style: none;
}

ul li {
  height: 50px;
  position: relative;
  margin: 10px 0;
  cursor: pointer;
}

ul li .fa {
  font-size: 20px;
  color: #fff;
}

.instagram div.logo-container {
  background: #bc2a8d;
}

.instagram div.slider {
  background: #dd94c6;
}

.telegram div.logo-container {
  background: #0088cc;
}

.telegram div.slider {
  background: #179cde;
}

.slider {
  content: "";
  max-width: inherit;
  position: absolute;
  top: 0;
  left: 51px;
  width: 0px;
  height: 50px;
  background: #eebb5c;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.slider p {
  font-family: arial;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 900;
  color: #fff;
  text-align: center;
  margin-top: 0.4rem;

  opacity: 0;
}

ul li:hover .slider {
  width: auto;
}

ul li:hover .slider p {
  opacity: 1;
}

/* youtube link */
.youtube {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 160px;
  text-align: center;
  padding: 15px 10px;
  background: #bb0000;
  border-radius: 5px;
}

.youtube a {
  text-decoration: none;
  color: #fff;
  text-transform: capitalize;
  letter-spacing: 1px;
}

